/* Componente DescrizioneInput aggiornato con Popper */
function DescrizioneInput({ value, onChange, suggestions, onSelectSuggestion }) {
  const anchorRef = useRef(null);
  return (
    <div style={{ position: 'relative', flex: 1 }} ref={anchorRef}>
      <TextField label="Descrizione" value={value} onChange={onChange} fullWidth required />
      <Popper open={Boolean(suggestions && suggestions.length)} anchorEl={anchorRef.current} style={{ zIndex: 2000 }} placement="bottom-start">
        <Paper style={{ width: anchorRef.current ? anchorRef.current.clientWidth : undefined }}>
          <ul style={{ listStyle: 'none', padding: 0, margin: 0, maxHeight: '150px', overflowY: 'auto' }}>
            {suggestions.map((sug, index) => (
              <li key={index} onClick={() => onSelectSuggestion(sug)} style={{ padding: '0.8rem', cursor: 'pointer' }}>
                {sug.descrizione}
              </li>
            ))}
          </ul>
        </Paper>
      </Popper>
    </div>
  );
}

/* Componente SortableRow con drag handle allineato */
function SortableRow({ id, index, prod, selected, onSelect, onDelete }) {
  const { attributes, listeners, setNodeRef, transform, transition } = useSortable({ id });
  const style = { transform: CSS.Transform.toString(transform), transition };

  return (
    <tr ref={setNodeRef} style={style}>
      <td style={{ padding: '8px', textAlign: 'center', whiteSpace: 'nowrap', verticalAlign: 'middle' }}>
        <Checkbox
          checked={selected}
          onClick={(e) => { 
            e.stopPropagation(); 
            onSelect(index);
          }}
          size="small"
        />
        <span
          {...listeners}
          {...attributes}
          style={{
            display: 'inline-flex',
            alignItems: 'center',
            justifyContent: 'center',
            cursor: 'grab',
            marginLeft: '8px',
            verticalAlign: 'middle',
          }}
        >
          <DragIndicatorIcon fontSize="small" />
        </span>
      </td>
      <td style={{ padding: '8px', textAlign: 'center', verticalAlign: 'middle' }}>{index + 1}</td>
      <td style={{ padding: '8px', verticalAlign: 'middle' }}>{prod.description}</td>
      <td style={{ padding: '8px', textAlign: 'center', verticalAlign: 'middle' }}>{prod.unit}</td>
      <td style={{ padding: '8px', textAlign: 'center', verticalAlign: 'middle' }}>{prod.quantity}</td>
      <td style={{ padding: '8px', textAlign: 'center', verticalAlign: 'middle' }}>
        {formatAmount(prod.price)} 
      </td>
      <td style={{ padding: '8px', textAlign: 'right', verticalAlign: 'middle' }}>
        {formatAmount(prod.total)} 
      </td>
      <td style={{ padding: '8px', textAlign: 'center', verticalAlign: 'middle' }}>
        <DeleteButton onClick={(e) => { e.stopPropagation(); onDelete(index); }}>
          <DeleteIcon />
        </DeleteButton>
      </td>
    </tr>
  );
}

function NuovoPreventivo() {
  // Stato per tutti i prodotti caricati dall'API
  const [allProducts, setAllProducts] = useState([]);
  const [suggestions, setSuggestions] = useState([]);
  
  // Stato per il nuovo prodotto
  const [newProduct, setNewProduct] = useState({ description: '', unit: '', quantity: 1, price: 0, total: 0 });
  // Altri stati
  const [products, setProducts] = useState([]);
  const [selectedRows, setSelectedRows] = useState([]);
  const [customer, setCustomer] = useState({ name: '', surname: '', address: '' });
  const [subject, setSubject] = useState('');
  const [open, setOpen] = useState(false);

  // Carica tutti i prodotti al montaggio della pagina
  useEffect(() => {
    const fetchAllProducts = async () => {
      try {
        const response = await fetch('http://localhost:8080/api/prodotti/', {
            method: "GET"
        }); // Sostituisci con il tuo endpoint
        const data = await response.json();
        setAllProducts(data || []);
      } catch (error) {
        console.error('Errore nel recupero di tutti i prodotti', error);
      }
    };
    fetchAllProducts();
  }, []);

  // Gestione dell'autocompletamento
  const handleDescriptionChange = (e) => {
    const value = e.target.value;
    setNewProduct((prev) => ({ ...prev, description: value }));
    if (value.length >= 3) {
      const filtered = allProducts.filter((prod) =>
        prod.descrizione.toLowerCase().includes(value.toLowerCase())
      );
      setSuggestions(filtered);
    } else {
      setSuggestions([]);
    }
  };

  const handleSelectSuggestion = (suggestion) => {
    setNewProduct({
      description: suggestion.descrizione,
      unit: suggestion.um,
      quantity: suggestion.quantity || 1,
      price: suggestion.price || 0,
      total: (suggestion.quantity || 1) * (suggestion.price || 0),
    });
    setSuggestions([]);
  };

  // Gestione degli altri campi
  const handleQuantityChange = (e) => {
    const quantity = parseFloat(e.target.value) || 0;
    setNewProduct((prev) => ({ ...prev, quantity, total: quantity * prev.price }));
  };

  const handlePriceChange = (e) => {
    const price = parseFloat(e.target.value) || 0;
    setNewProduct((prev) => ({ ...prev, price, total: prev.quantity * price }));
  };

  const handleUnitChange = (e) => {
    setNewProduct((prev) => ({ ...prev, unit: e.target.value }));
  };

  const handleAddProduct = () => {
    if (!newProduct.description || !newProduct.unit || !newProduct.quantity || !newProduct.price) {
      alert('Tutti i campi sono obbligatori');
      return;
    }
    setProducts([...products, newProduct]);
    setNewProduct({ description: '', unit: '', quantity: 1, price: 0, total: 0 });
    setSuggestions([]);
    setOpen(false);
  };

  /* Eliminazione di un prodotto singolo */
  const handleDeleteProduct = (index) => {
    const updated = products.filter((_, i) => i !== index);
    setProducts(updated);
    setSelectedRows((prev) => prev.filter((i) => i !== index));
  };

  /* Eliminazione di pi righe */
  const handleDeleteSelected = () => {
    const updated = products.filter((_, i) => !selectedRows.includes(i));
    setProducts(updated);
    setSelectedRows([]);
  };

  /* Drag & Drop con dnd-kit */
  const handleDragEnd = (event) => {
    const { active, over } = event;
    if (!over || active.id === over.id) return;
    const oldIndex = products.findIndex((_, index) => `product-${index}` === active.id);
    const newIndex = products.findIndex((_, index) => `product-${index}` === over.id);
    setProducts((items) => arrayMove(items, oldIndex, newIndex));
  };

  /* Gestione selezione riga */
  const handleSelectRow = (index) => {
    setSelectedRows((prev) =>
      prev.includes(index) ? prev.filter((i) => i !== index) : [...prev, index]
    );
  };

  const handleSelectAll = () => {
    if (selectedRows.length === products.length) {
      setSelectedRows([]);
    } else {
      setSelectedRows(products.map((_, index) => index));
    }
  };

  // Calcola l'imponibile
  const imponibile = () => products.reduce((acc, prod) => acc + prod.total, 0);

  return (
    <Container maxWidth="lg">
      <Typography variant="h4" align="center" gutterBottom>
        Nuovo Preventivo
      </Typography>

      {/* Sezione dati cliente e preventivo */}
      <Box display="flex" flexDirection={{ xs: 'column', md: 'row' }} gap={3} mb={3}>
        <Paper elevation={3} sx={{ flex: 1, padding: 3 }}>
          <Typography variant="h6" gutterBottom>
            Dati Cliente
          </Typography>
          <Grid container spacing={2}>
            <Grid item xs={12} sm={6}>
              <TextField
                label="Nome"
                value={customer.name}
                onChange={(e) => setCustomer({ ...customer, name: e.target.value })}
                fullWidth
                required
              />
            </Grid>
            <Grid item xs={12} sm={6}>
              <TextField
                label="Cognome"
                value={customer.surname}
                onChange={(e) => setCustomer({ ...customer, surname: e.target.value })}
                fullWidth
                required
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                label="Indirizzo"
                value={customer.address}
                onChange={(e) => setCustomer({ ...customer, address: e.target.value })}
                fullWidth
                required
              />
            </Grid>
          </Grid>
        </Paper>
        <Paper elevation={3} sx={{ flex: 1, padding: 3 }}>
          <Typography variant="h6" gutterBottom>
            Dati Preventivo
          </Typography>
          <TextField
            label="Oggetto"
            value={subject}
            onChange={(e) => setSubject(e.target.value)}
            fullWidth
            required
          />
        </Paper>
      </Box>

      {/* Sezione dettagli prodotti */}
      <Paper elevation={3} sx={{ padding: 3, mb: 3 }}>
        <Typography variant="h6" gutterBottom>
          Dettaglio Prodotti
        </Typography>
        <DndContext collisionDetection={closestCenter} onDragEnd={handleDragEnd}>
          <SortableContext items={products.map((_, index) => `product-${index}`)} strategy={verticalListSortingStrategy}>
            <table style={{ width: '100%', borderCollapse: 'collapse' }}>
              <thead>
                <tr>
                  <th style={{ borderBottom: '1px solid #ccc', padding: '8px', whiteSpace: 'nowrap' }}>
                    <Checkbox
                      checked={products.length > 0 && selectedRows.length === products.length}
                      onChange={handleSelectAll}
                    />
                  </th>
                  <th style={{ borderBottom: '1px solid #ccc', padding: '8px' }}>#</th>
                  <th style={{ borderBottom: '1px solid #ccc', padding: '8px' }}>Descrizione</th>
                  <th style={{ borderBottom: '1px solid #ccc', padding: '8px' }}>Unit di Misura</th>
                  <th style={{ borderBottom: '1px solid #ccc', padding: '8px' }}>Quantit</th>
                  <th style={{ borderBottom: '1px solid #ccc', padding: '8px' }}>Importo Unitario</th>
                  <th style={{ borderBottom: '1px solid #ccc', padding: '8px' }}>Totale</th>
                  <th style={{ borderBottom: '1px solid #ccc', padding: '8px' }}>Azioni</th>
                </tr>
              </thead>
              <tbody>
                {products.map((prod, index) => (
                  <SortableRow
                    key={`product-${index}`}
                    id={`product-${index}`}
                    index={index}
                    prod={prod}
                    selected={selectedRows.includes(index)}
                    onSelect={handleSelectRow}
                    onDelete={handleDeleteProduct}
                  />
                ))}
              </tbody>
              <tfoot>
                <tr>
                  <td colSpan={5} style={{ textAlign: 'right', padding: '8px', borderTop: '2px solid #ccc' }}>
                    <strong>Imponibile:</strong>
                  </td>
                  <td colSpan={2} style={{ textAlign: 'right', padding: '8px', borderTop: '2px solid #ccc' }}>
                    <strong>{formatAmount(imponibile())} </strong>
                  </td>
                  <td style={{ borderTop: '2px solid #ccc' }}></td>
                </tr>
              </tfoot>
            </table>
          </SortableContext>
        </DndContext>
        <Box mt={2} display="flex" justifyContent="center" alignItems="center" gap={2}>
          <CustomButton onClick={() => setOpen(true)}>
            <AddCircleOutlineIcon style={{ marginRight: '0.5rem' }} />
            Aggiungi Prodotto
          </CustomButton>
          {selectedRows.length > 0 && (
            <CancelButton onClick={handleDeleteSelected}>
              Elimina Selezionati
            </CancelButton>
          )}
        </Box>
      </Paper>

      {/* Sezione pulsanti finali */}
      <Box display="flex" flexDirection="column" gap={2} alignItems="center" mb={4}>
        <Box display="flex" gap={2}>
          <SaveButton onClick={() => alert('Preventivo salvato!')}>
            Salva Preventivo
          </SaveButton>
          <PrintButton onClick={() => window.print()}>
            Stampa Preventivo
          </PrintButton>
          <MailButton onClick={() => alert('Preventivo inviato via mail!')}>
            Invia via Mail
          </MailButton>
        </Box>
        <Box>
          <NewQuotationButton onClick={() => {
            setCustomer({ name: '', surname: '', address: '' });
            setSubject('');
            setProducts([]);
            setSelectedRows([]);
          }}>
            Nuovo Preventivo
          </NewQuotationButton>
        </Box>
      </Box>

      {/* Modale per aggiungere un prodotto */}
      <Dialog open={open} onClose={() => setOpen(false)} fullWidth maxWidth="lg">
        <DialogTitle>Aggiungi Prodotto</DialogTitle>
        <DialogContent>
          <Box display="flex" flexWrap="wrap" gap={2} mt={2}>
            <Box sx={{ flex: 4 }}>
              <DescrizioneInput
                value={newProduct.description}
                onChange={handleDescriptionChange}
                suggestions={suggestions}
                onSelectSuggestion={handleSelectSuggestion}
              />
            </Box>
            <Box sx={{ flex: 2 }}>
              <TextField
                select
                label="Unit di Misura"
                value={newProduct.unit}
                onChange={handleUnitChange}
                fullWidth
                required
              >
                <MenuItem value="ac">A corpo</MenuItem>
                <MenuItem value="mq">Metri quadrati</MenuItem>
                <MenuItem value="ml">Metri lineari</MenuItem>
              </TextField>
            </Box>
            <Box sx={{ flex: 1 }}>
              <TextField
                label="Quantit"
                type="number"
                value={newProduct.quantity}
                onChange={handleQuantityChange}
                fullWidth
                required
              />
            </Box>
            <Box sx={{ flex: 1 }}>
              <TextField
                label="Importo Unitario"
                type="number"
                value={newProduct.price}
                onChange={handlePriceChange}
                fullWidth
                required
              />
            </Box>
            <Box sx={{ flex: 1 }}>
              <TextField
                label="Totale"
                type="number"
                value={newProduct.total}
                InputProps={{ readOnly: true }}
                fullWidth
              />
            </Box>
          </Box>
        </DialogContent>
        <DialogActions>
          <CancelButton onClick={() => setOpen(false)}>Annulla</CancelButton>
          <CustomButton onClick={handleAddProduct}>Aggiungi</CustomButton>
        </DialogActions>
      </Dialog>
    </Container>
  );
}

export default NuovoPreventivo;
