import React, { useEffect, useState } from 'react';
import { Container, Typography, Paper, Table, TableHead, TableRow, TableCell, TableBody, Button, Box } from '@mui/material';
import { styled } from '@mui/material/styles';
import { useNavigate } from 'react-router-dom';

// Pulsanti personalizzati per le azioni nello Storico
const EditButton = styled(Button)(({ theme }) => ({
  background: 'linear-gradient(135deg, #42a5f5, #1e88e5)',
  color: '#fff',
  marginRight: theme.spacing(1),
  textTransform: 'none',
  '&:hover': {
    background: 'linear-gradient(135deg, #1e88e5, #42a5f5)',
  },
}));

const PrintButton = styled(Button)(({ theme }) => ({
  background: 'linear-gradient(135deg, #ffa726, #fb8c00)',
  color: '#fff',
  textTransform: 'none',
  '&:hover': {
    background: 'linear-gradient(135deg, #fb8c00, #ffa726)',
  },
}));

// Funzione per formattare gli importi (2 decimali, virgola)
const formatAmount = (value) =>
  Number(value).toLocaleString('it-IT', {
    minimumFractionDigits: 2,
    maximumFractionDigits: 2,
  });

function Storico() {


    const [quotations, setQuotations] = useState([])

    useEffect(() => {
        const callRequest = async () => {
          try {
            const response = await fetch('http://localhost:8080/api/preventivi/', {
                method: "GET"
            }); // Sostituisci con il tuo endpoint
            const data = await response.json();
            setQuotations(data || []);
          } catch (error) {
            console.error('Errore nel recupero di tutti i prodotti', error);
          }
        };
        callRequest();
      }, []);

  const navigate = useNavigate();

  const handleEdit = (id) => {
    // Naviga verso la pagina di modifica, passando l'id come parametro
    navigate(`/edit/${id}`);
  };

  const handlePrint = (id) => {
    // In un'app reale si potrebbe aprire una pagina di anteprima o chiamare window.print()
    alert(`Stampa preventivo ${id}`);
  };

  return (
    <Container maxWidth="lg">
      <Typography variant="h4" align="center" gutterBottom>
        Storico Preventivi
      </Typography>
      <Paper elevation={3} sx={{ padding: 3 }}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell>ID</TableCell>
              <TableCell>Cliente</TableCell>
              <TableCell>Oggetto</TableCell>
              <TableCell align="right">Totale ()</TableCell>
              <TableCell align="center">Azioni</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {quotations.map((quote) => (
              <TableRow key={quote.id}>
                <TableCell>{quote.id}</TableCell>
                <TableCell>{quote.datiCliente.nome + " "+ quote.datiCliente.cognome}</TableCell>
                <TableCell>{quote.datiPreventivo.oggetto}</TableCell>
                <TableCell align="right">{formatAmount(quote.datiPreventivo.prodotti.reduce((a,v) =>  a = a + v.price , 0 ))}</TableCell>
                <TableCell align="center">
                  <EditButton onClick={() => handleEdit(quote.id)}>Modifica</EditButton>
                  <PrintButton onClick={() => handlePrint(quote.id)}>Stampa</PrintButton>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </Paper>
    </Container>
  );
}

export default Storico;
